___CSS_LOADER_EXPORT___.push([module.id, `.detail {
    display: grid;
    grid-gap: 1.545em;
    font-size: 1em;
    margin-top: 1em;
}

.detail-img {
    width: 100%;
    height: 370px;
    object-fit: cover;
    object-position: center;
}

.detail-name-address-rating {
    display: inline-block;
}

.icon-primary {
    color: var(--btn);
}

.detail h3 {
    font-size: 1.5em;
    padding: 1em 0 0 0;
    background-color: var(--primary-color);
    font-weight: bold;
}

.detail-desc {
    text-align: justify;
    color: darkgrey;
    margin-bottom: 0.5em;
    font-style: italic;
}

.category {
    padding: 0.3em 1em;
    color: var(--primary-color);
    background-color: var(--btn);
    margin-right: 2px;
    border: 1px solid transparent;
    border-radius: 0px 10px 0px 10px;
}

.detail-menu h4 {
    font-weight: bold;
    font-size: 1.37em;
    padding: 1em;
    color: var(--btn);
    background-color: var(--primary-color);
}

.detail-food {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-content: center;
}

.detail-food li {
    padding: 0.5em 0;
    display: block;
    text-decoration: none;
    background-color: var(--primary-color);
    border: 0.5px solid var(--secondary-color);
    border-width: 0 0 0.5px;
}

.detail-drink {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-content: center;
}

.detail-drink li {
    padding: 0.5em 0;
    display: block;
    text-decoration: none;
    background-color: var(--primary-color);
    border: 0.5px solid var(--secondary-color);
    border-width: 0 0 0.5px;
}

.detail-info {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding-left: 0;
    margin-bottom: 0;
}

.detail-info li {
    position: relative;
    display: block;
    padding: 0.6em;
    color: var(--font-color);
    text-decoration: none;
    background-color: var(--primary-color);
}

  /* DETAIL REVIEW */
.detail-review {
    max-width: 100%;
    font-size: 1em;
    text-align: center;
}

.detail-review-item {
    color: var(--font-color);
    background-color: var(--primary-color);
    margin-bottom: 1.5em;
    padding: 1em 1em;
}

.detail-review-item:last-child {
    margin-bottom: 0;
}

.review-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em 1em;
    color: var(--btn);
    border-bottom: 1px solid var(--font-color);
}

.review-name {
    font-weight: bold;
    display: flex;
    align-items: center;
}

.review-date {
    font-size: 0.8em;
    font-weight: lighter;
}

.review-body {
    padding: 1em;
    text-align: left;
}

.category:hover,
.detail-food li:hover,
.detail-drink li:hover,
.detail-review-item:hover {
    box-shadow: 0px 10px 13px -7px var(--secondary-color),
        5px 5px 15px 5px var(--secondary-color);
}

/* LIKE BUTOON */
.like {
    font-size: 18px;
    background-color: var(--btn);
    color: white;
    border: 0;
    border-radius: 50%;
    width: 55px;
    height: 55px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 16px;
    right: 16px;
    z-index: 10;
}`, "",{"version":3,"sources":["webpack://./src/styles/resto-detail.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,iBAAiB;IACjB,cAAc;IACd,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,iBAAiB;IACjB,uBAAuB;AAC3B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,sCAAsC;IACtC,iBAAiB;AACrB;;AAEA;IACI,mBAAmB;IACnB,eAAe;IACf,oBAAoB;IACpB,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,2BAA2B;IAC3B,4BAA4B;IAC5B,iBAAiB;IACjB,6BAA6B;IAC7B,gCAAgC;AACpC;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,YAAY;IACZ,iBAAiB;IACjB,sCAAsC;AAC1C;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,qBAAqB;AACzB;;AAEA;IACI,gBAAgB;IAChB,cAAc;IACd,qBAAqB;IACrB,sCAAsC;IACtC,0CAA0C;IAC1C,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,qBAAqB;AACzB;;AAEA;IACI,gBAAgB;IAChB,cAAc;IACd,qBAAqB;IACrB,sCAAsC;IACtC,0CAA0C;IAC1C,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,6BAA6B;IAC7B,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,cAAc;IACd,cAAc;IACd,wBAAwB;IACxB,qBAAqB;IACrB,sCAAsC;AAC1C;;EAEE,kBAAkB;AACpB;IACI,eAAe;IACf,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,wBAAwB;IACxB,sCAAsC;IACtC,oBAAoB;IACpB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,gBAAgB;IAChB,iBAAiB;IACjB,0CAA0C;AAC9C;;AAEA;IACI,iBAAiB;IACjB,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,YAAY;IACZ,gBAAgB;AACpB;;AAEA;;;;IAII;+CAC2C;AAC/C;;AAEA,gBAAgB;AAChB;IACI,eAAe;IACf,4BAA4B;IAC5B,YAAY;IACZ,SAAS;IACT,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,YAAY;IACZ,WAAW;IACX,WAAW;AACf","sourcesContent":[".detail {\r\n    display: grid;\r\n    grid-gap: 1.545em;\r\n    font-size: 1em;\r\n    margin-top: 1em;\r\n}\r\n\r\n.detail-img {\r\n    width: 100%;\r\n    height: 370px;\r\n    object-fit: cover;\r\n    object-position: center;\r\n}\r\n\r\n.detail-name-address-rating {\r\n    display: inline-block;\r\n}\r\n\r\n.icon-primary {\r\n    color: var(--btn);\r\n}\r\n\r\n.detail h3 {\r\n    font-size: 1.5em;\r\n    padding: 1em 0 0 0;